// Fonts
@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,600);

// Variables
@import "variables";

img.user-avatar {
  display: inline-block;
  margin: 0 auto;
  height: 220px;
  width: auto;
  max-width: 220px;
  max-height: 220px;
  border: 3px solid #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0,0,0,.15);
  overflow: hidden;
}
